import React, { Component } from 'react';
import './canvasGraphic.css';
import { nextLcgWithProps, nextLcgFloatWithProps } from "./generators"

// See https://blog.cloudboost.io/using-html5-canvas-with-react-ff7d93f5dc76

class CanvasGraphicWithProps extends React.Component {
  constructor(props) {
		super(props)
		this.state = 
    {  a: props.a,
       c: props.c,
       m: props.m,
       n: props.n,
    seed: props.seed   
    };
  }
  
    


      componentDidMount() {
            const prngCanvas = this.refs.prngCanvas
            const ctx = prngCanvas.getContext("2d")

            var y = 0;
            //draw();
            function draw() {
              for(var x = 0; x < 50; x++) {

                if(nextLcgFloatWithProps(6,0,11,1) < 0.5) {
                  console.log("in DidMount")
                  ctx.fillStyle="gray"  
                  ctx.fillRect(x+50, y, 1, 1);
                }else {
                  ctx.fillStyle="blue"  
                  ctx.fillRect(x+50, y, 1, 1);
                }
              }
              y++;
              if(y < 50) {
                requestAnimationFrame(draw);
              }
            }
          }

      componentDidUpdate() {
            const prngCanvas = this.refs.prngCanvas
            const ctx = prngCanvas.getContext("2d")
            const a = this.props.a, c = this.props.c, m = this.props.m, seed = this .props.seed
            var y = 0;
            var count = 0;
            const nLoops = 200;
            draw();
            function draw() {
              for(var x = 0; x < nLoops; x+=1) {
                if(nextLcgFloatWithProps(a,c,m,seed,count) < 0.5) {
                  ctx.fillStyle="blue"  
                  ctx.fillRect(x, y, 1, 1);
                } else {
                  ctx.fillStyle="pink"  
                  ctx.fillRect(x, y, 1, 1);
                }
                count ++
              }
              y+=1;
              if(y < nLoops) {
                requestAnimationFrame(draw);
              } else {
                count = 0
              }
            }
          }
      
    render() {
  
      
      
        return(
            <div>
          <div>
              <canvas ref="prngCanvas" width={300} height={300} />
          </div>
        </div>
        )
      }
    }

    export default CanvasGraphicWithProps