//import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
//import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import NotificationsIcon from '@material-ui/icons/Notifications';
//import ListItem from '@material-ui/core/ListItem';
//import ListItemIcon from '@material-ui/core/ListItemIcon';
//import ListItemText from '@material-ui/core/ListItemText';
//import InboxIcon from '@material-ui/icons/MoveToInbox';
//import MailIcon from '@material-ui/icons/Mail';
import Badge from '@material-ui/core/Badge';

import {
    AppBar, Toolbar, IconButton, Typography,
    Drawer, CssBaseline, MenuList, MenuItem
  } from '@material-ui/core'
import { compose } from 'recompose'

const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'centre',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 20,
  },
  hide: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
});

class PersistentDrawerLeft extends React.Component {
  state = {
    open: false,
    title: "Introduction to Monte Carlo simulation."
  };

  title = this.state.title

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };
  
  handleDrawerClose = () => {
    this.setState({ open: false });
  };
  
  render() {
    const { classes, theme, location: { pathname }, children, writers} = this.props;
    const { open } = this.state;


    const drawer = (
        <div>
          
          <MenuList>
            <MenuItem component={Link} to="/" selected={'/' === pathname} onClick = {() => (this.title=this.state.title)}>
              Home
            </MenuItem>
            <MenuItem component={Link} to="/writers" selected={'/writers' === pathname} onClick = {() => (this.title="Writers")}>
              Writers
            </MenuItem>
            <MenuItem component={Link} to="/discretisationComponentPath/" onClick = {() => (this.title="Discretisation")}>
              Discretisation
            </MenuItem>
            <MenuItem component={Link} to="/pricingProblemPath" onClick = {() => (this.title="The Pricing Problem")}>
              Pricing problem
            </MenuItem>
            <MenuItem component={Link} to="/pricingProblemNavVersionPath" onClick = {() => (this.title="The Pricing Problem (navVersion)")}>
              Pricing problem (nav)
            </MenuItem>
            <MenuItem component={Link} to="/pricingProblemNavVersionPath_duplicate" onClick = {() => (this.title="The Pricing Problem (navVersion duplicate)")}>
              Pricing problem (nav_dup)
            </MenuItem>
            <MenuItem component={Link} to="/pricingProblemNavVersionPath_2" onClick = {() => (this.title="The Pricing Problem (navVersion_2")}>
              Pricing problem (nav_2)
            </MenuItem>
            <MenuItem component={Link} to="/pricingUnderlyingNavVersionPath" onClick = {() => (this.title="Pricing the underlying (navVersion)")}>
              Pricing underlying (nav)
            </MenuItem>
            <Divider />
            <MenuItem component={Link} to="/pricingProblemNavVersionNextPath" onClick = {() => (this.title="Pricing the underlying (navVersion with next)")}>
              Pricing underlying (nav-next)
            </MenuItem>
            <MenuItem component={Link} to="/pseudoRngsPath" onClick = {() => (this.title="Pseudo random number generators")}>
              Pseudo rngs
            </MenuItem>
            <MenuItem component={Link} to="/rngs_uniform_msmPath" onClick = {() => (this.title="Pseudo random number generators")}>
              Middle-square method
            </MenuItem>
            <MenuItem component={Link} to="/rngs_uniform_p1Path" onClick = {() => (this.title="Pseudo random number generators")}>
              Uniform p1
            </MenuItem>
            <MenuItem component={Link} to="/rngs_uniform_p2Path" onClick = {() => (this.title="Pseudo random number generators")}>
              Uniform p2
            </MenuItem>
            <MenuItem component={Link} to="/uniform_lcgPath" onClick = {() => (this.title="Pseudo random number generators")}>
              LCG example
            </MenuItem>
            <MenuItem component={Link} to="/uniform_codeMathsPath" onClick = {() => (this.title="Pseudo random number generators")}>
              Code-maths examples
            </MenuItem>
            <MenuItem component={Link} to="/uniform_codeMaths_canvasPath" onClick = {() => (this.title="Pseudo random number generators")}>
              Code-maths canvas
            </MenuItem>
            <MenuItem component={Link} to="/randomnessRamseyPath" onClick = {() => (this.title="Pseudo random number generators")}>
              Van der Waerden
            </MenuItem>
            <MenuItem component={Link} to="/CLT_deMoivrePath" onClick = {() => (this.title="Pseudo random number generators")}>
              deMoivre
            </MenuItem>
            <MenuItem component={Link} to="/materialui_GridPath" onClick = {() => (this.title="Pseudo random number generators")}>
              Grid example
            </MenuItem>
            <Divider />
            <MenuList>
              {writers.map(({ id, name }) => {
                const to = `/writers/${id}`
  
                return <MenuItem
                  to={to}
                  key={id}
                  className={classes.nested}
                  component={Link}
                  selected={to === pathname}
                >
                  {name}
                </MenuItem>
              })}
            </MenuList>
          </MenuList>
        </div>
      )

    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar
          position="absolute"
          className={classNames(classes.appBar, {
            [classes.appBarShift]: open,
          })}
        >
          <Toolbar disableGutters={!open} >
            <IconButton
              color="inherit"
              aria-label="Open drawer"
              onClick={this.handleDrawerOpen}
              className={classNames(classes.menuButton, open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h4" color="inherit" noWrap className={classes.title}>
              {this.title}
            </Typography>
            <IconButton component={Link} to="/discretisationComponentPath" onClick = {() => (this.title="Discretisation - left (fix the shiftring chevrons)")} color="inherit">              
              <ChevronLeftIcon />             
            </IconButton>
            <IconButton component={Link} to="/discretisationComponentPath" onClick = {() => (this.title="Discretisation - right (fix the shiftring chevrons)")} color="inherit">              
              <ChevronRightIcon />             
            </IconButton>
          </Toolbar>
        </AppBar>
        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={open}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
        <div className={classes.drawerHeader}>
            <IconButton onClick={this.handleDrawerClose}>
              {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
            </IconButton>
          </div>
        {drawer}
        </Drawer>
        <main
          className={classNames(classes.content, {
            [classes.contentShift]: open,
          })}
        >
          <div className={classes.drawerHeader} />
          {children}
        </main>
      </div>
    );
  }
}

PersistentDrawerLeft.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

//export default withStyles(styles, { withTheme: true })(PersistentDrawerLeft);
export default compose(
    withRouter, 
    withStyles(styles, { withTheme: true }))(PersistentDrawerLeft)
