testRepeat = (x) => {
	
	var results = []
	var repeatsAt = 0
	for (var i=0;i<100;i++){
		var rand = x[i+1];
		if(results[rand]) {
			repeatsAt = i 
			//console.log("i,x,x+1: ", i, x[i], x[i+1])
			break
		} else {
		}
		results[rand] = true
	}
	return repeatsAt
}
// ---------------------------------------------------------------
	seriesLcg2 = (a, c, m, seed, n, dp) => {
		console.log("seriesLcg2")
		var x = [], u = []
		x[0] = [seed]
		u[0] = [(seed / m).toFixed(dp)]
		for(var i=1;i<n;i++){
			x[i] = [(a * x[i-1] + c) %m]
			u[i] = [(x[i]/m).toFixed(dp)]
		}
		console.log("u = ", u)
		return {x, u}
	}

