import React from "react";
import MathJax from 'react-mathjax';
import './css/style.css';

const eulerMaruyama_S = `\\hat{S}_{t+1} = \\hat{S}_{t} + a(\\hat{S}_{t},t)h + b(\\hat{S}_{t},t)\\Delta W_{t+1}`;

const gbm_S = `dS(t) = a(S(t),t)dt + b(S(t),t)dW(t)`;

class Discretisation extends React.Component {
	render(){
		return(
			<MathJax.Provider>
                <div className="navClass">
			
            
            <h2>The Euler-Maruyama approach.</h2>
            <ul>
            	<li> If the sde is </li>
            	<MathJax.Node formula={gbm_S} />
            	<li> Its `Euler-Maruyama' discrete-time approximation is the stochastic difference equation 	
                <MathJax.Node formula={eulerMaruyama_S} />
            	where <MathJax.Node inline formula={'h'} /> is the length of time between <MathJax.Node inline formula={'t'} /> and <MathJax.Node inline formula={'t+1'} />, <MathJax.Node inline formula={'\\hat{S}_t'} /> is the discrete-time approximation to the continuous-time <MathJax.Node inline formula={'S(th)'} /> and <MathJax.Node inline formula={'\\Delta W_{t+1} \\sim N(0,h)'} /></li>
            	<li> There is a substantial literature about the many ways in which to approximate the sde.</li>
            </ul>
            </div>
            </MathJax.Provider>
        )
    }
}    

export default Discretisation