// -------------------------  lcg code  -------------------------

//var a = 1103515245,
//    c = 12345,
//    m = Math.pow(2, 31),
//    seed = 1;

var a = 6,
    c = 0,
    m = 11,
    seed = 1;

const nextLcg = () => {
    seed = (a * seed + c)%m
    //console.log("seed no props = ", seed)
    return seed
}    

const nextLcgFloat = () => {
    return nextLcg() / m
}
// ----------------------------------------------------------------
// ----------------------------------------------------------------

const nextLcgWithProps = (a,c,m,seed0,count) => {
    if (count === 0){
        seed = seed0
    }
    seed = (a * seed + c)%m   
    //console.log("a, c, m, seed with props = ", a, c, m, seed)
    return seed
}    

const nextLcgFloatWithProps = (a,c,m,seed0,count) => {
    //console.log(" **** a , c, m, seed with props float = ", a, c, m, seed)
    //console.log(" count = ", count)
    return nextLcgWithProps(a,c,m,seed0,count) / m
}
// ----------------------------------------------------------------
// ----------------------------------------------------------------
const seriesLcg = (a, c, m, seed, n, dp) => {
    var x = [], u = []
    x[0] = [seed]
    u[0] = [(seed / m).toFixed(dp)]
    for(var i=1;i<n;i++){
        x[i] = [(a * x[i-1] + c) %m]
        u[i] = [(x[i]/m).toFixed(dp)]
    }
    return {x, u}
}




// -------------------------  msm code  -------------------------

var digits = 10
    // var seed = 9999199999 - good pattern!
var msmSeed = 9999199999


const nextRand = () => {
    var n = (msmSeed * msmSeed).toString()
    while(n.length < digits * 2){
        n = "0" + n;
    }
    var start = Math.floor(digits/2),
        end = start + digits;
    msmSeed = parseInt(n.substring(start,end))
    
    return msmSeed
}

const nextRandFloat = () => {
    return nextRand()/9999999999 
}




export {nextRand , nextRandFloat, nextLcg, nextLcgFloat, nextLcgWithProps, nextLcgFloatWithProps, seriesLcg}