import React from "react";
import MathJax from 'react-mathjax';
import './css/style.css';

const rng_1 = `\\begin{eqnarray}
					x_{i+1} & = & (ax_{i} + c)~~\\mbox{mod}~~m\\\\
					u_{i+1} & = & x_{i+1}/m
					\\end{eqnarray}`;
const rng_2 = `b~\\mbox{mod}~ m  \\equiv   b - m\\times floor(b/m)`;
const rng_3 = `5~\\mbox{mod}~2  =  5 - [2\\times floor(5/2)] = 5 - [2\\times floor(2.5)]= 5 - [2\\times 2]= 5-4 = 1`;
const rng_3_1 = `6~\\mbox{mod}~11  =  6 - [2\\times floor(6/11)] = 6 - [2\\times floor(0.5454')]= 6 - [2\\times 0]= 6`;
const rng_4 = `\\begin{eqnarray*}
				x_{1} & = & 6~\\mbox{mod}~11\\\\
					  & = &  6\\\\
				 u_{1} & = & 6/11\\\\
							& = & 0.54'
				\\end{eqnarray*}`
const rng_5 = `1, 6, 3, 7, 9, 10, 5, 8, 4, 2, 1, \\mbox{(what comes next?)}`


class  Rngs extends React.Component {
	render(){
		return(
			<MathJax.Provider>
			<div className={"navClass"}>

				<h1>Uniform (0,1) distribution.</h1>
				<h2>Linear Congruential Generators.</h2>

				<ul>
				<li>The first equation generates 'random' positive integers <MathJax.Node inline formula={`x`}/> in the interval <MathJax.Node inline formula={`(0,m-1)`}/>...</li>
				<li>...which the second converts into rational numbers in the interval <MathJax.Node inline formula={`(0,(m-1)/m)`}/>. </li>
					<MathJax.Node formula={rng_1} />
					where
					<MathJax.Node formula={rng_2} />
					<li>For example </li>
					<MathJax.Node formula={rng_3} />
					<MathJax.Node formula={rng_3_1} />
				</ul>
                
			</div>
			</MathJax.Provider>
		)
	}
}

export default Rngs

