// Print an array [[1] [2] [3]] as a row with commas between the elements.

function HtmlArray(a)  {
    var str = "<table>"
        for (var j=0; j<a.length-1; j++){
        str +=  "<td>" + a[j][0] + ",&nbsp&nbsp&nbsp</td>"
        }
        str +=  "<td>" + a[a.length-1];
      str += "</table>";
      
      return (
        str
      )
    }

export default HtmlArray