import React from "react";
import MathJax from 'react-mathjax';
import './css/style.css';

const binomialProb = `P[S_n=k] = \\frac{n!}{k!(n-k)!}p^k(1-p)^{n-k}`;
const binomialLimit = '\\underset{n \\rightarrow \\infty}{lim}~~P\\left[a \\leq \\frac{S_n - np}{\\sqrt{np(1-p)}} \\leq b\\right] = \\frac{1}{\\sqrt{2\\pi}} \\int_a^b exp\\left[ -\\frac{1}{2}x^2 \\right]dx'





class CLT_deMoivre  extends React.Component {
	render(){
		return(
			<MathJax.Provider>
			<div>

				<h1>Binomial and Normal distributions: deMoivre's result</h1>

				<ul>
				<li>For a string of n 0's and 1's generated independently and with probabilities p and (1-p) the sum of the string's values is distributed as:
				<MathJax.Node formula={binomialProb} />
				</li>
                <li>De Moivre discovered that:
				<MathJax.Node formula={binomialLimit} />
				</li>
				</ul>

			</div>
			</MathJax.Provider>
		)
	}
}

export default CLT_deMoivre

