import './App.css';

class App extends Component {
  state = {
    writers: []
  }

  async componentDidMount() {
    const writers = await (await fetch('http://localhost:3005/writers?_embed=texts')).json()

    this.setState({ writers })
  }
  render() {
    const { writers } = this.state

    const x =  newX(1,6,0,11)
    const multix = uniform(.5,9,4,1300,26)

    return (
      <div>
      <BrowserRouter>
      <PersistentDrawer writers={writers} title="test">
        <Switch>
          <Route exact path="/" render={() => <div>Home</div>} />
          <Route path="/writers" render={
            props => <Writers {...props} writers={writers} />
          } />
          <Route path="/discretisationComponentPath/" component={Discretisation} />
          <Route path="/pricingProblemPath/" component={PricingProblem}  />
          <Route path="/pricingProblemNavVersionPath/" component={PricingProblem_navVersion} />
          <Route path="/pricingProblemNavVersionPath_duplicate/" component={PricingProblem_navVersion_duplicate} />
          <Route path="/pricingProblemNavVersionPath_2/" component={PricingProblem_navVersion_2} />
          <Route path="/pricingUnderlyingNavVersionPath/" component={PricingUnderlying_navVersion} />
          <Route path="/pricingProblemNavVersionNextPath/" component={PricingProblem_navVersion_next} />
          <Route path="/pseudoRngsPath/" component={PseudoRngs} />
          <Route path="/rngs_uniform_p1Path/" component={Rngs_uniform_p1} />
          
          <Route path="/rngs_uniform_p2Path/" component={Rngs_uniform_p2} />
          <Route path="/uniform_lcgPath/" component={Uniform_lcg} />
          <Route path="/rngs_uniform_MsmPath/" component={Rngs_uniform_msm} />
          <Route path="/uniform_codeMathsPath/" component={Uniform_codeMaths} />
          <Route path="/uniform_codeMaths_canvasPath/" component={Uniform_codeMaths_canvas} />
          <Route path="/randomnessRamseyPath/" component={RandomnessRamsey} />
          <Route path="/CLT_deMoivrePath/" component={CLT_deMoivre} />
          <Route path="/materialui_GridPath/" component={CenteredGrid} />
          <Route component={NotFound} />
          <Uniform_lcg />
        </Switch>
      </PersistentDrawer >
    </BrowserRouter>
      </div>
    );
  }
}

export default App;
