import React from "react";
import MathJax from 'react-mathjax';
import { newX, uniform } from "../mathCalculators/uniform_lcg_math"
import HtmlArrayRowVector from "../formattingTools/HtmlArrayRowVector"
import Parser from "html-react-parser"




const x =  newX(1,6,0,11)
const multix = uniform(.5,9,4,1300,26)

class Uniform_lcg  extends React.Component {
	render(){
		return(
			
			<div>
				<h2> Linear congruential generator example</h2>
      			{`x_{i-1}`} = {x[0]}<br></br>
      			{`x_{i}`} = {x[1]}<br></br>
      			{`x series`} = {multix}
      			{Parser(HtmlArrayRowVector(multix))}
      		</div>
			
		)
	}
}

export default Uniform_lcg