const newX = (oldX, a, c, m) => {
    let z=0, z_over_m=0, floor_zz=0, mfloor_zz=0, z_mod_m=0, newX=0
        z = a*oldX + c
        z_over_m = z / m
        floor_zz = Math.floor(z_over_m) 
        mfloor_zz = m * floor_zz
        z_mod_m = z - mfloor_zz
        newX = z_mod_m
    return [oldX,newX]
}

const uniform = (x0,a,c,m,n) =>  {
    let x = [], u = [];
    x[0] = [x0]
    u[0] = [x0/m]
    for(var i=1;i<n;i++){
        x[i] = [newX(x[i-1],a,c,m)[1]]
        u[i] = [x[i] / m]
    }
    return u
} 


export {newX, uniform}
