import React, { Component } from 'react';
import './canvasGraphic.css';
import { nextLcg, nextLcgFloat } from "./generators"

// See https://blog.cloudboost.io/using-html5-canvas-with-react-ff7d93f5dc76

class CanvasGraphicLcg extends React.Component {
  

    componentDidMount() {
        const prngCanvas = this.refs.prngCanvas
        const ctx = prngCanvas.getContext("2d")
        
        var y = 0;
        draw();
        function draw() {
          for(var x = 0; x < 200; x++) {
            
            if(nextLcgFloat() < 0.5) {
              ctx.fillStyle="gray"  
              ctx.fillRect(x, y, 1, 1);
            }
          }
          y++;
          if(y < 200) {
            requestAnimationFrame(draw);
          }
        }
      }

    render() {
      console.log("canvasprops: ", this.props.data)
        return(
            <div>
          <div>
              <canvas ref="prngCanvas" width={200} height={200} />
          </div>
        </div>
        )
      }
    }

    export default CanvasGraphicLcg