import React from "react";
import MathJax from 'react-mathjax';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import Typography from '@material-ui/core/Typography';
import './css/style.css';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

const pricing_SandC = `\\begin{eqnarray*}
\\hat{S}_t & = & \\frac{1}{(1+r_s/12)^{10}}\\sum_{i=1}^n \\hat{S}_{t+10,i}\\\\
  & = & 0.9053 \\times \\sum_{i=1}^n \\hat{S}_{t+10,i}\\\\
   & \\approx & 5\\\\
   \\hat{C}_t & = & \\frac{1}{(1+r_s/12)^{10}}\\sum_{i=1}^n \\mbox{max}[\\hat{S}_{t+10,i}-3,0]\\\\
  & = & 0.9053 \\times 2.49\\\\
   & \\approx & 2.28
\\end{eqnarray*} `;

class  PricingUnderlying extends React.Component {
	continue = e => {
		e.preventDefault();
		this.props.nextStep();
	  };
	
	  back = e => {
		e.preventDefault();
		this.props.prevStep();
	  };

	render(){

		return(
			
			<div >
				{//<AppBar >
        //<Toolbar>
        //  <IconButton  color="inherit" aria-label="Menu">
        //    <MenuIcon />
        //  </IconButton>
        //  <Typography variant="h3" color="inherit" >
				//	'Pricing' the underlying.
        //  </Typography>
        //</Toolbar>
      	//</AppBar>
				}
					
				
		

			<MathJax.Provider>
			<div className="navClass">
			<MathJax.Node formula={pricing_SandC} />



			</div>
			</MathJax.Provider>
			
		<MuiThemeProvider>
		<React.Fragment>
			<RaisedButton
            label="Continue"
            primary={true}
            style={styles.button}
            onClick={this.continue}
          />
          <RaisedButton
            label="Back"
            primary={false}
            style={styles.button}
            onClick={this.back}
          />
		  </React.Fragment>
		  </MuiThemeProvider>
		  </div>
		)
	}
}

const styles = {
	button: {
		margin: 15,
	}
	};
	
const divStyle = {
	bar: {
		color: 'purple',
		fontSize: 90,
		background: 'green'
	}
	};

export default  PricingUnderlying 

