import React from "react";
import MathJax from 'react-mathjax';


import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import Typography from '@material-ui/core/Typography';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

const euroCall_S = `\\begin{eqnarray*}C(t,T) & = & e^{-r(T-t)}\\hat{E}\\left[max\\left(S(T)-K,0\\right)\\right]\\\\
				dS(t) & = & rSdt + \\sigma SdW(t)
				\\end{eqnarray*}`;
const bs_S = `C(t,T) = S(t)N(d_1) - e^{-r(T-t)}KN(d_2)`;


class PriceProblem extends React.Component {
	continue = e => {
		e.preventDefault();
		this.props.nextStep();
	  };
	
	  back = e => {
		e.preventDefault();
		this.props.prevStep();
	  };
	render(){
		return(
			<div >
			{//<AppBar >
        //<Toolbar>
        //  <IconButton  color="inherit" aria-label="Menu">
        //    <MenuIcon />
        //  </IconButton>
        //  <Typography variant="h3" color="inherit" >
        //    The pricing problem
        //  </Typography>
        //</Toolbar>
			//</AppBar>
			}

			<MathJax.Provider>
			<div className="navClass">


				<ul>
				<li>We obtained this in Part I </li>
					<MathJax.Node formula={euroCall_S} />

				<li> For this particular problem we have a closed-form analytical solution	
				<MathJax.Node formula={bs_S} /></li>
				<li> For many other problems there are no analytical solutions and we have to resort to numerical methods such as Monte Carlo. </li>
				</ul>
			</div>
			</MathJax.Provider>

			
			<MuiThemeProvider>
			<React.Fragment>
				<RaisedButton
				label="Continue"
				primary={true}
				style={styles.button}
				onClick={this.continue}
			  />
			  <RaisedButton
				label="Back"
				primary={false}
				style={styles.button}
				onClick={this.back}
			  />
			  </React.Fragment>
			  </MuiThemeProvider>
			  </div>
		)
	}
}
const styles = {
	button: {
	  margin: 15
	}
  };
export default PriceProblem